.class-vote {
  background: #2a273b;
  width: 100%;
  padding: 19px 70px;
}
.class-vote > .ant-col {
  height: 100vh;
}
.class-vote > .ant-col .vote-main-bottom {
  width: 490px;
  position: absolute;
  bottom: 150px;
  left: 50%;
  transform: translateX(-50%);
}
.class-vote > .ant-col .vote-main-bottom .ant-btn {
  width: 220px;
  height: 44px;
  border-radius: 22px !important;
  background: #27cda2;
  color: #fff;
  border: none;
}
.class-vote > .ant-col .vote-main-bottom .ant-btn:nth-of-type(1) {
  margin-right: 50px;
  background: none;
  color: #27CDA2;
  border: 1px solid #27CDA2;
}
.class-vote p {
  font-size: 16px;
  color: #fff;
  line-height: 16px;
  border-left: 2px solid #fff;
  padding-left: 8px;
}
.class-vote .ant-form-item-label > label {
  color: rgba(255, 255, 255, 0.8);
}
.class-vote .voteNumBtn {
  color: rgba(255, 255, 255, 0.8);
  margin: 0 10px;
  cursor: pointer;
}
.class-vote .ant-radio-button-wrapper {
  border: 1px solid rgba(255, 255, 255, 0.6);
  width: 180px;
  height: 121px;
  background: none;
  color: #ffffff;
  font-size: 18px;
  margin-right: 31px;
  border-radius: 5px;
}
.class-vote .ant-radio-button-wrapper:last-child {
  margin: 0;
}
.class-vote .ant-radio-button-wrapper-checked {
  border: 1px solid #06a5d8;
  border-left: none;
  background: rgba(255, 255, 255, 0.1);
}
.class-vote .ant-radio-button-wrapper::before {
  content: none;
}
.class-vote .ant-radio-button-wrapper-checked:first-child {
  border-left: 1px solid #06a5d8;
}
.class-vote .ant-radio-button-wrapper-checked:first-child:hover {
  border-left: 1px solid #06a5d8;
}
.class-vote .ant-radio-button-wrapper-checked:hover {
  border: #06a5d8 1px solid;
  border-left: none;
}
.class-vote .ant-form-item {
  margin: 30px 0;
}
